@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600');

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f2f7f9;
  font-family: 'Open Sans', sans-serif;
}

.topbar {
  position: relative;
  height: 80px;
  width: 1360px;
  margin: auto;
  background-color: #fcfefe;
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  padding-left: 80px;
  display: table;
}

.navbar {
  display: table-row;
}

.navbarcell {
  display: table-cell;
  vertical-align: middle;
  height: 80px;
}

.cell1 {
  width: 161px;
  border-right: 1px solid #f1f6fa;
  background-image: url("img/logo.png");
  background-repeat: no-repeat;
  background-position: 0 50%;
  background-size: 121px;
}

.cell2 {
  padding: 0;
  margin: 0;
  padding-left: 95px;
  color: #8b98ac;
  width: calc(100%-500px);
}

.cell2 ul {
  height: 100%;
  display: table-row;
}

.cell2 li {
  display: table-cell;
  height: 80px;
  padding-right: 60px;
  vertical-align: middle;
}

.cell2 .arrowicon {
  min-width: 78px;
  color: #3a3f49;
  font-weight: 600;
  background-image: url("img/arrow.svg");
  background-repeat: no-repeat;
  background-position: calc(100% - 40px) 50%;
  background-size: 9px;
}

.cell2 .arrowicon:hover {
  background-image: url("img/uparrow.svg");
}

.cell2 .arrowicon:hover .sm2 {
  display: block;
}

.cell2 .placeicon {
  width: 18px;
  padding-right: 6px;
  background-image: url("img/place.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 18px;
}

.cell3 {
  width: 500px;
}

.part1 {
  width: 1440px;
  height: 670px;
  margin: auto;
  background-image: url("img/bg.png");
  background-position: 100% -70px;
  background-repeat: no-repeat;
  border-bottom: 1px solid #e1ebf4;
}

.part2 {
  width: 1280px;
  margin: 20px auto;
}

.part1TextBlock {
  padding-top: 200px;
  padding-left: 200px;
  font-size: 36px;
  color: #3a3f49;
}

.part1btn {
  width: 80px;
  font-size: 14px;
  margin-top: 40px;
  padding: 10px 20px;
  color: #fff;
  border-radius: 20px;
  background-color: #0086bb;
}

.part1btn:hover {
  background-color: #009ad6;
}

.part2TextBlock {
  font-size: 24px;
  color: #3a3f49;
  display: table-row;
}

.part2TextBlock div {
  display: table-cell;
}

.part2TextBlock div:nth-child(1) {
  padding-right: 20px;
  width: 180px;
}

.part2TextBlock div:nth-child(2) {
  width: 1010px;
}

.part2TextBlock div:nth-child(3) {
  width: 20px;
  text-align: right;
  padding-right: 30px;
  color: #96a3b4;
  font-size: 14px;
  background: url("img/morearrow.png") 100% 20px no-repeat;
}

.part2TextBlock div:nth-child(3):hover {
  color: #3b3f4a;
  font-weight: 600;
  background: url("img/morearrowovr.png") 100% 20px no-repeat;
}

.part2ImgBlock {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 50px;
}
.part2img {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 16px;
  width: 308px;
  color: #fff;
  font-size: 14px;
  height: 267px;
  border-radius: 10px;
  background: url("img/img1.png") 50% 50% no-repeat;

}
.part2img1text{
  position: relative;
  padding: 20px;
}
.part2img1text2 {
  position: relative;
  padding: 20px;
  font-size: 18px;
  font-weight: 600;
}

.dark {
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  position: absolute;
  display: none;
  width: 308px;
  color: #fff;
  font-size: 14px;
  height: 267px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.4);
}
.dark div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  text-align: left;
}
.darkbuy {
  background: url("img/coin.png") 0 0 no-repeat;
  width: 20px;
  margin-top: 24px;
  margin-right: 30px;
  height: 20px;
}

.darklike {
  position: relative;
  z-index: 10;
  background: url("img/like.png") 0 0 no-repeat;
  width: 20px;
  margin-top: 161px;
  height: 20px;
  margin-right: 30px;
}

.liked {
    background: url("img/liked.png") 0 0 no-repeat;
}

.part2img:hover .dark {
  display: -ms-flexbox;
  display: flex;
}

.part1Text {
  font-size: 14px;
}

/* Иконки (Колокольчик, лупа), кнопка Create Event */

.icon {
  display: table-cell;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  vertical-align: middle;
}

.search {
  width: 18px;
  height: 18px;
  background-image: url("img/search.svg");
  background-repeat: no-repeat;
  background-size: 16px;
}

.search:hover {
  background-image: url("img/searchb.svg");
  background-size: 16px;
}

.bell {
  width: 18px;
  height: 20px;
  background-image: url("img/bell.png");
  background-size: 18px;
  background-repeat: no-repeat;
  margin-right: 30px;
  margin-left: 30px;
}

.bell:hover {
  background-image: url("img/bellb.png");
}

.bell::after {
  content: "";
  margin-left: 17px;
  margin-bottom: 15px;
  width: 5px;
  height: 5px;
  display: inline-block;
  border-radius: 50%;
  background-color: #009ad6;
}

.event {
  height: 80px;
  font-size: 14px;
  padding-left: 30px;
  padding-right: 30px;
  color: #8b98ac;
  border-left: 1px solid #f1f6f9;
  border-right: 1px solid #f1f6f9;
  width: 100px;
  text-align: center;
}

.event:hover {
  background-color: #009ad6;
  color: white;
}

.account {
  font-size: 14px;
  color: #009ad6;
  padding-left: 20px;
  height: 80px;
  background-image: url("img/ava.png");
  background-position: 110px 50%;
  width: 240px;
}

.colorless {
  color: #8b98ac;
}

.submenu {
  position: absolute;
  top: 80px;
  width: 180px;
  background-color: #fcfefd;
  -webkit-box-shadow: 0 5px 8px rgba(0, 0, 0, 0.1), inset 0 5px 8px -5px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 8px rgba(0, 0, 0, 0.1), inset 0 5px 8px -5px rgba(0, 0, 0, 0.2);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: none;
  font-size: 14px;
  color: #8b98ac;
  font-weight: 400;
}

.sm1 {
  right: calc((100% - 1440px) + 100px);
}

.sm2 {
  left: calc((100% - 1440px) + 300px);
  padding-left: 20px;
  padding-top: 20px;
  width: 130px;
}

.submenu li {
  display: block;
  margin-bottom: 10px;
}

.sm2 li {
  padding: 0;
  height: 24px;
}

.sm1 li {
  width: 110px;
}

.submenu li:hover {
  color: #3b3f4a;
  font-weight: 600;
}

.sm1 li:last-child {
  border-top: 1px solid #e6f0f2;
  padding-top: 5px;
}

.account:hover .sm1 {
  display: block;
}



